/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --violet: hsl(263, 55%, 52%);
  --lightViolet: hsl(264, 82%, 70%);
  --grayBlue: hsl(217, 19%, 35%);
  --lightGrayBlue: hsla(217, 19%, 35%, 0.25);
  --darkBlue: hsl(219, 29%, 14%);
  --white: hsl(0, 0%, 100%);
  --lightGray: hsl(0, 0%, 81%);
  --grayishBlue: hsl(210, 46%, 95%);
}

:root {
  --barlow: "Barlow Semi Condensed", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.3rem;
  font-family: var(--barlow);
}

.heading-secondary {
  font-size: 1.3rem;
}

.heading-tertiary {
  font-size: 2rem;
}

main {
  display: grid;
  align-content: center;
  min-height: 100vh;
  background-color: var(--grayishBlue);
}

.wrapper {
  max-width: 32.7rem;
  width: 100%;
  margin: 0 auto;
  display: grid;
  align-content: center;
}
@media (min-width: 48em) {
  .wrapper {
    max-width: 64rem;
  }
}
@media (min-width: 64em) {
  .wrapper {
    max-width: 76rem;
  }
}
@media (min-width: 75em) {
  .wrapper {
    max-width: 110rem;
  }
}

.grid {
  display: grid;
  align-content: center;
  justify-content: center;
  row-gap: 2.4rem;
}
@media (min-width: 75em) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas: "daniel daniel jonathan kira" "jeanette patrick patrick kira";
    row-gap: 2.4rem;
    column-gap: 3rem;
  }
}

.review {
  align-self: center;
  /* Reviewer's Name */
}
.review__content {
  padding: 3.2rem;
  padding-top: 2.6rem;
  border-radius: 8px;
  box-shadow: 40px 60px 50px -47px var(--lightGrayBlue);
  color: var(--white);
}
.review__profile {
  margin-bottom: 1.8rem;
  display: grid;
  grid-template-columns: 2.8rem 1fr;
  column-gap: 1.7rem;
}
.review__img {
  border-radius: 50%;
  align-self: center;
}
.review__status {
  opacity: 0.5;
}
.review__title {
  margin-bottom: 2.4rem;
}
.review__description {
  opacity: 0.7;
}
.review__bg {
  position: absolute;
  top: 0;
  right: 2.4rem;
  z-index: 1;
}
@media (min-width: 75em) {
  .review__bg {
    right: 8rem;
  }
}
.review__daniel {
  background-color: var(--violet);
  position: relative;
}
.review__daniel .review__title {
  margin-bottom: 4rem;
  position: relative;
  z-index: 2;
}
@media (min-width: 75em) {
  .review__daniel .review__title {
    margin-bottom: 1.6rem;
  }
}
@media (min-width: 75em) {
  .review__daniel {
    grid-area: daniel;
  }
}
.review__daniel .review__img {
  border: 2px solid var(--lightViolet);
}
.review__jonathan {
  background-color: var(--grayBlue);
}
@media (min-width: 75em) {
  .review__jonathan .review__title {
    margin-bottom: 1.6rem;
  }
}
@media (min-width: 75em) {
  .review__jonathan {
    grid-area: jonathan;
  }
}
.review__jeanette {
  background-color: var(--white);
  color: var(--grayBlue);
}
@media (min-width: 75em) {
  .review__jeanette {
    grid-area: jeanette;
  }
}
.review__patrick {
  background-color: var(--darkBlue);
}
.review__patrick .review__img {
  border: 2px solid var(--violet);
}
@media (min-width: 75em) {
  .review__patrick {
    grid-area: patrick;
  }
}
.review__kira {
  background-color: var(--white);
  color: var(--grayBlue);
}
@media (min-width: 75em) {
  .review__kira {
    grid-area: kira;
  }
}